.comment-dock{height:1.1rem}.comment-dock .comment-fixed{display:-webkit-box;display:-ms-flexbox;display:flex;z-index:1000;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;position:fixed;left:0;right:0;bottom:0;height:1.1rem;padding:.2rem .3rem;box-sizing:border-box;border-top:1px solid #C0C0C0;background:#F5F5F5}.comment-dock .comment-fixed .input{border:1px solid #0CBF79;width:100%;padding:0 .2rem;border-radius:.08rem;height:.7rem;line-height:.7rem;font-size:.3rem;background:#FFF;color:#cbcbcb;box-sizing:border-box;vertical-align:middle}.comment-dock .comment-fixed button{max-width:2rem;width:28%;margin-left:.2rem;border:0;border-radius:.08rem;background:#11BF79;height:.7rem;text-align:center;color:#FFF;box-sizing:border-box;vertical-align:middle}.comment-dock .comment-fixed button .iconfont{font-size:.45rem}.comment-dock .comment-reply-wrap{position:fixed;z-index:899999;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,0.4)}.comment-dock .comment-reply-wrap .comment-reply{position:absolute;z-index:999999;left:0;bottom:0;width:100%;background:#f5f5f5}.comment-dock .comment-reply-wrap .comment-reply-hd{height:1rem;line-height:1rem;font-size:.28rem;display:-webkit-box;display:-ms-flexbox;display:flex;z-index:1000;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-pack:space-between;-ms-flex-pack:justify;justify-content:space-between}.comment-dock .comment-reply-wrap .comment-reply-hd .pull{padding:0 .2rem;width:2em;display:inline-block}.comment-dock .comment-reply-wrap .comment-reply-hd .title{font-size:.36rem;font-weight:bold}.comment-dock .comment-reply-wrap .comment-reply-hd .disabled{color:#bfbfbf}.comment-dock .comment-reply-wrap .comment-reply-content{padding:.2rem}.comment-dock .comment-reply-wrap .comment-reply-content textarea{box-sizing:border-box;vertical-align:middle;border:1px solid #0CBF79;width:100%;height:2rem;font-size:.3rem;padding:.2rem;border-radius:.08rem}.comment-dock .comment-reply-wrap .comment-reply-ft{min-height:.5rem;line-height:1rem}.comment-dock .reply-transition{-webkit-transition:all .3s ease;transition:all .3s ease;background:rgba(0,0,0,0.4)}.comment-dock .reply-transition .comment-reply{-webkit-transition:all .3s ease;transition:all .3s ease;-webkit-transform:translate(0, 0);transform:translate(0, 0)}.comment-dock .reply-enter,.comment-dock .reply-leave{background:rgba(0,0,0,0)}.comment-dock .reply-enter .comment-reply,.comment-dock .reply-leave .comment-reply{-webkit-transform:translate(0, 100%);transform:translate(0, 100%)}


